'use client';

import {Todo} from "../lib/api";
import React from "react";

interface TodoItemProps {
  todo: Todo;
  onStatusChange: (id: number, status: 'pending' | 'completed') => void;
  onDelete: (id: number) => void;
}

export function TodoItem({ todo, onStatusChange, onDelete }: TodoItemProps) {
  const toggleStatus = () => {
    const newStatus = todo.status === 'pending' ? 'completed' : 'pending';
    onStatusChange(todo.id, newStatus);
  };

  return (
    <div 
      className={`
        bg-white p-4 rounded-lg shadow-md border-l-4
        ${todo.status === 'completed' ? 'border-green-500' : 'border-yellow-500'}
      `}
    >
      <div className="flex justify-between items-start">
        <div className="flex-1">
          <h3 className="font-semibold text-lg">{todo.title}</h3>
          <p className="text-gray-600 mt-1">{todo.description}</p>
          <p className="text-sm text-gray-500 mt-2">
            Due: {new Date(todo.due_date).toLocaleDateString()}
          </p>
        </div>
        <div className="flex gap-2">
          <button
            onClick={toggleStatus}
            className={`px-3 py-1 rounded-md text-sm font-medium
              ${todo.status === 'completed' 
                ? 'bg-yellow-100 text-yellow-700' 
                : 'bg-green-100 text-green-700'
              }`}
          >
            {todo.status === 'completed' ? 'Mark Pending' : 'Mark Complete'}
          </button>
          <button
            onClick={() => onDelete(todo.id)}
            className="px-3 py-1 rounded-md text-sm font-medium bg-red-100 text-red-700"
          >
            Delete
          </button>
        </div>
      </div>
    </div>
  );
}